.slidePage {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-size: 14px;
  background-color: #EEEEEE;
  width: 100%;
  height: 106px;
}
.slidePage .demo-image {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.slidePage .block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  margin-top: 12px;
}
.slidePage .demonstration {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  text-align: center;
  line-height: 16px;
  font-weight: 400;
  margin-top: 8px;
}
.slidePage .white_layer {
  background-color: #fff;
  height: 100%;
  width: 100%;
}
.slidePage .a_div_label_top {
  height: 28px;
  width: 100%;
  font-size: 12px;
}
.slidePage .a_div_label {
  height: 28px;
  width: 96%;
  margin-left: 18px;
  font-size: 12px;
}
.slidePage .div_lable_font {
  height: 100px;
  border: 1px solid #1e1e1e;
}
.slidePage .el_carousel__item_image {
  width: 160px;
  height: 160px;
  margin-top: 20px;
}
.slidePage .el_carousel__item_style {
  text-align: center;
  height: 106px;
}
.slidePage /deep/ .el-carousel__indicators--horizontal .el-carousel__indicator--horizontal {
  padding: 12px 2px !important;
}
.slidePage /deep/ .el-carousel__indicators--horizontal .el-carousel__indicator--horizontal button {
  width: 4px;
  height: 4px;
  background: #d8d8d8;
  border-radius: 2.5px;
}
.slidePage /deep/ .el-carousel__indicators--horizontal .el-carousel__indicator--horizontal.is-active button {
  width: 10px;
  height: 4px;
  background: #276ff5;
  border-radius: 2.5px;
}
